.app,
.loader,
.error {
  position: relative;
  width: 100%;
  height: 100%;
  background: #f6f6fc;
  background: -webkit-linear-gradient(top, #f6f6fc 0%, #ffffff 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#f6f6fc), to(#ffffff));
  background: -o-linear-gradient(top, #f6f6fc 0%, #ffffff 100%);
  background: linear-gradient(to bottom, #f6f6fc 0%, #ffffff 100%);
  overflow: hidden;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  min-height: 100vh;
}

footer {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  width: 100%;
  height: 80px;
  text-align: center;
  color: #625fe2;
}

.soundcloud-logo {
  display: inline-block;
  width: 6.5rem;
  height: 1rem;
  margin-bottom: -0.125rem;
  background: url('../images/soundcloud-logo.png') center;
  background-size: contain;
}

.loader,
.error {
  display: none;
  z-index: 2;
  will-change: opacity;
}

.error {
  -webkit-animation: 1s fadein forwards;
          animation: 1s fadein forwards;
  will-change: opacity;
}

.loader-loading,
.error-display {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -80%);
      -ms-transform: translate(-50%, -80%);
          transform: translate(-50%, -80%);
}

.error-display img {
  width: 400px;
  max-width: 100%;
}

.loader-loading img {
  max-width: 100%;
}

.loader-loading {
  -webkit-animation: 2s fade-alternate 0.3s infinite alternate;
          animation: 2s fade-alternate 0.3s infinite alternate;
  will-change: opacity;
}

.loader-loading::after,
.error-display::after {
  position: absolute;
  top: 100%;
  left: 0;
  height: 0.7rem;
}

.loader-loading::after {
  content: '';
  margin-top: 0.5rem;
  background: #8e8ce9;
  -webkit-animation: 1s loading-bar 0.3s ease-in-out infinite;
          animation: 1s loading-bar 0.3s ease-in-out infinite;
  will-change: left;
}

.error-display::after {
  content: 'Oops something went wrong';
  width: 100%;
  font-size: 1.5rem;
  text-align: center;
  color: #8e8ce9;
}

.is-fadeIn {
  opacity: 0;
  -webkit-animation: fadein 0.7s ease-in 1s forwards;
          animation: fadein 0.7s ease-in 1s forwards;
}

.header {
  width: 100%;
  height: 80px;
}

.logo-header {
  position: absolute;
  top: 2rem;
  left: 3rem;
  width: 11.25rem;
  height: 2.25rem;
  background: url('../images/logo.svg') center no-repeat;
  background-size: contain;
}

.app::before {
  content: '';
  position: fixed;
  bottom: 3rem;
  left: 35%;
  width: 1200px;
  height: 29rem;
  background: url('../images/aaa.svg') bottom right no-repeat;
  background-size: contain;
}

@media screen and (max-width: 1024px) {
  .app::after {
    left: 50%;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    opacity: 0.8;
  }
  .logo-header {
    padding-top: 2rem;
    display: block;
    margin: 0 auto;
    position: relative;
    top: 0;
    left: 0;
  }
  footer {
    display: none;
  }
}
@media screen and (max-height: 400px) {
  footer {
    display: none;
  }
}

@-webkit-keyframes loading-bar {
  0% {
    left: 0;
    width: 0;
  }
  50% {
    left: 0;
    width: 100%;
  }
  100% {
    left: 100%;
    width: 0;
  }
}

@keyframes loading-bar {
  0% {
    left: 0;
    width: 0;
  }
  50% {
    left: 0;
    width: 100%;
  }
  100% {
    left: 100%;
    width: 0;
  }
}

@-webkit-keyframes fade-alternate {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.8;
  }
}

@keyframes fade-alternate {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.8;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



